header{
    width: 100%;
    padding: 2rem 0 6rem 0;
    position: relative;
    background-color: #014E56;

    nav{
        max-width: 1110px;
        margin: auto;
        display: flex;
        justify-content: space-between;

        .hamburger{
            display: none;
        }
    
        ul{
            display: flex;
            align-items: center;
            gap: 2rem;
            list-style: none;

            li{

                i{
                    color: #FFF;
                }

                a{
                    text-decoration: none;
                    color: #FFF;
                    font-family: Livvic;
                    font-size: 18px;
                    font-style: normal;
                    font-weight: 600;
                    line-height: 28px;
                }

                button{
                    background: none;
                    width: 153px;
                    height: 48px;
                    border-radius: 24px;
                    border: 2px solid #FFF;
                    cursor: pointer;
                    color: #FFF;
                    font-family: Livvic;
                    font-size: 18px;
                    font-style: normal;
                    font-weight: 600;
                    line-height: 28px
                }
            }
        }

        // @media (max-width: ;)
    }



    .wrapper{
        max-width: 1110px;
        margin: auto;
        margin-top: 60px;
        display: flex;

        .wrapper_card{
            width: 50%;

            h1{
                color: #FFF;
                font-family: Livvic;
                font-size: 64px;
                font-style: normal;
                font-weight: 700;
                line-height: 100px;
            }

            .line{
                width: 50px;
                height: 4px;
                margin-bottom: 30px;
                background: #F67E7E;    
            }

            p{
                color: #FFF;
                font-family: Livvic;
                font-size: 18px;
                font-style: normal;
                font-weight: 600;
                line-height: 28px;
            }
        }        
    } 

    .IMG{
        position: absolute;
        bottom: 0;
        right: 0;
    }
}